/**
 * Couldn't find the gift card.
 */
export class GiftCardNotFoundWixError extends Error {
  /** @hidden */
  httpCode: number;
  /** @hidden */
  statusCode: string;
  /** @hidden */
  applicationCode: string;
  /** @hidden */
  name: string;
  /** @hidden */
  errorType: string;
  /** @hidden */
  spiErrorData: object;

  constructor() {
    super('GiftCardNotFound');

    this.httpCode = 404;
    this.statusCode = 'NOT_FOUND';
    this.applicationCode = 'GIFT_CARD_NOT_FOUND';
    this.name = 'GiftCardNotFound';
    this.errorType = 'SPI';
    this.spiErrorData = {
      name: 'GiftCardNotFound',
      applicationCode: 'GIFT_CARD_NOT_FOUND',
    };
  }

  /** @hidden */
  static readonly __type = 'wix_spi_error';
}

